.swiper-slide {
  width: 1013px;
}

.gallery {
  background-image: linear-gradient(to right, #fafbff66 0%, #f5f5f500 0%),
    url('../images/bg-1x.jpg');

  @media (min-device-pixel-ratio: 2),
    (min-resolution: 192dpi),
    (min-resolution: 2dppx) {
    background-image: linear-gradient(to right, #e8e8f100 0%, #f0f1ff00 0%),
      url('../images/bg-1x.jpg');
  }
}
